.about {
    background-color: #ffffff;
    height: 90vh;
    padding: 50px 70px;
    animation: fadeIn 1.5s ease-in-out;
    font-family: "Nunito", sans-serif;
}

.aboout-contant {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    margin: 20px 2px;
}

.aboout-contant h1 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 800px; 
    margin-top: -470px;
}

.aboout-contant p {
    margin-top: -230px;
    margin-left: -210px;
    padding: 40px;
    line-height: 20px;
    letter-spacing: 1px;
}

.list {
    margin-top: -300px;
    list-style: none;
    line-height: 40px;
    transition: 0.3s;
}

.list hr {
    outline: none;
    border: none;
    width: 50%;
    height: 5px;
    border-radius: 50px;
    background: linear-gradient(264deg, #fd5c63 -5.09%, #fd5c63);
    margin-left: 150px;
    margin-top: -20px;
}

.list li:hover {
    transform: scale(1.05);
    transition: 0.3s;
}

@media screen and (max-width: 768px) {
    .about {
        padding: 30px 20px;
    }

    .about img{

        display: none;

    }
    .aboout-contant {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    .aboout-contant h1 {
        width: 100%;
        margin-top: 0;
        font-size: 1.8rem;
        display: flex;
        flex-direction: row;
        justify-content: center;

    }

    .aboout-contant p {
        margin-top: 0;
        margin-left: 0;
        padding: 20px 40px;
        font-size: 1rem;
        line-height: 1.5;
    }

    .list {
        margin-top: 0;
        margin-left: 30px;
        margin: 0;
        margin-left: 150px;

    }

    .list hr {
        width: 80%;
        margin-left: 160;
        align-items: center;
    }

    .list li {
        font-size: 1.1rem;
    }
   
}



@media screen and (max-width: 480px) {
    .about {
        padding: 20px 10px;
    }

    .aboout-contant {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    .aboout-contant h1 {
        width: 100%;
        margin-top: 0;
        font-size: 1.5rem;
    }

    .aboout-contant p {
        margin-top: 0;
        margin-left: 0;
        padding: 15px;
        font-size: 0.9rem;
        line-height: 1.4;
    }

   
    .list {
        margin-top: 0;
        text-align: center;
    }

    .list hr {
        width: 70%;
        margin-left: 0;
    }

    .list li {
        font-size: 1rem;
    }
   
}

 
    
