.footer{
        height: 80vh;
        background: #ffffff;
        padding-top: 40px;
        color: #000000;
        font-family: "Nunito", sans-serif;
    }


.footer-title{
   
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;
    
}

.footer-title h2{
    color: rgb(0, 0, 0);
    font-size: 2.1rem;
    font-weight: 500;
    font-weight: bolder;
    text-transform: capitalize;
    line-height: 15rem;
}

.footer-title p{
    width: 600px;
    margin-top: -100px;
    font-family: "Nunito", sans-serif;

}

.logo{
        margin: 50px 0px;
        gap: 20px;
}

.footers{

    background-color: black;
    height: 15vh;

}
.footer-last p{

    margin-top: 50px;
    padding: 0 20px;
    font-size: 12px;
    
}

.footer-last{
    display: flex;
    justify-content: space-between;
    font-size: 15px;
    background-color: #000000;
    color: white;
    padding: 0 1px;
}

.footer-lastone{
    display: flex;
    margin-bottom: 1px;
    margin-left: -40px;

}

.footer-lastone h5{
    padding: 28px 200px;
    font-weight: lighter;
    letter-spacing: 2px;
    color: white;
    list-style: none;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
    .footer-title h2 {
        font-size: 2rem;
    }

    .footer-title p {
        font-size: 1rem;
        width: 90%;
    }

    .footer-last {
        flex-direction: column;
        text-align: center;
    }

    .footer-lastone {
        flex-direction: column;
        align-items: center;
    }

    .footer-last p {
        font-size: 0.9rem;
    }

    .footer-lastone a {
        font-size: 1rem;
    }
}