/* Contact Section */
.Contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 50px;
    padding: 90px;
    min-height: 100vh;
    background-color: black;
    color: white;
    animation: slideInLeft 1.5s ease-in-out;
    font-family: "Nunito", sans-serif;
}

/* Title Styling */
.contact-title {
    text-align: center;
    margin-bottom: 20px;
}

.contact-title h1 {
    font-size: 3rem;
    margin: 0;
}

.contact-title h2 {
    font-size: 1.5rem;
    color: rgba(255, 255, 255, 0.6);
    margin: 10px 0;
}

.contact-title p {
    font-size: 1rem;
    color: rgba(255, 255, 255, 0.8);
    max-width: 600px;
    margin: 0 auto;
}

/* Icon Section */
.icon {
    font-family: "Nunito", sans-serif;
    text-align: center;
    margin-top: -20px;
}

.icon div {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
}

.icon a {
    text-decoration: none;
    color: rgb(205, 204, 204);
    font-size: 1rem;
    margin-left: 10px;
}

.icon span {
    color: rgb(205, 204, 204);
    font-size: 1rem;
}

/* Contact Form */
.contact-form {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-family: "Nunito", sans-serif;
}

.contact-right {
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: flex-start;
    background-color: rgba(255, 255, 255, 0.05);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);
    padding: 30px;
    border-radius: 8px;
    max-width: 500px;
    width: 100%;
}

.contact-right label {
    color: #d8d8d8;
    font-size: 1rem;
    font-weight: 500;
}

.contact-right input,
.contact-right textarea {
    width: 100%;
    border: none;
    border-radius: 4px;
    background: rgba(255, 255, 255, 0.1);
    color: #fff;
    font-size: 1rem;
    padding: 10px;
    margin-top: 5px;
    outline: none;
    margin-left: -11px;
}

.contact-right textarea {
    resize: none;
    height: 120px;
}

.contact-right input:focus,
.contact-right textarea:focus {
    background: rgba(255, 255, 255, 0.15);
    border: 1px solid rgba(255, 255, 255, 0.3);
}

.contact-submit {
    background: rgb(227, 11, 11);
    border: none;
    color: white;
    border-radius: 50px;
    font-size: 1rem;
    padding: 10px 20px;
    cursor: pointer;
    transition: transform 0.3s ease, background 0.3s ease;
    margin-top: 10px;
}

.contact-submit:hover {
    transform: scale(1.1);
    background: rgb(200, 0, 0);
}

/* Responsive Design */
@media (max-width: 768px) {
    .Contact {
        padding: 50px 20px;
    }

    .contact-title h1 {
        font-size: 2.5rem;
    }

    .contact-title h2 {
        font-size: 1.2rem;
    }

    .contact-right {
        padding: 20px;
        max-width: 70%;
    }

    .contact-right input,
    .contact-right textarea {
        font-size: 0.9rem;
    }

    .contact-submit {
        font-size: 0.9rem;
        padding: 8px 16px;
    }
}

@media (max-width: 480px) {
    .Contact {
        padding: 40px 15px;
    }

    .contact-title h1 {
        font-size: 2rem;
    }

    .contact-title h2 {
        font-size: 1rem;
    }

    .contact-right {
        padding: 15px;
    }

    .contact-right input,
    .contact-right textarea {
        font-size: 0.8rem;
    }

    .contact-submit {
        font-size: 0.8rem;
        padding: 7px 14px;
    }
}
