@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&family=Playwrite+HU:wght@100..400&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Prompt:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Teko:wght@300..700&display=swap');

/* General Styles */
.container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 90vh;
    padding: 80px;
    background-color: #f6f3f0;
    animation: slideInLeft 1.5s ease-in-out;
    font-family: "Nunito", sans-serif;
    font-weight: bold;
}

.container-name h1,
.container-name h2,
.container-name h3,
.container-name h4,
.container-name img {
    color: #000;
    margin-bottom: 10px;
    animation: fadeIn ease-in-out;
    margin: 10px;
}

.container-name h1 {
    font-size: 4rem;
    animation-duration: 2s;
}

.container-name h2 {
    font-size: 4rem;
    animation-duration: 2s;
}

.container-name h3 {
    font-size: 2rem;
    color: #555;
    animation-duration: 2.5s;
}

.container-name h4 {
    font-size: 1.5rem;
    color: #777;
    animation-duration: 3s;
}

.container-name p {
    font-size: 1rem;
    line-height: 1.5;
    color: #555;
    max-width: 600px;
    animation: slideInRight 1.5s ease-in-out;
}

.container-name img {
    margin-left: 850px;
    margin-top: -500px;
    mix-blend-mode: darken;
}

.profile img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    animation: zoomIn 1.5s ease-in-out;
}

/* Animations */
@keyframes slideInLeft {
    from {
        transform: translateX(-100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slideInRight {
    from {
        transform: translateX(100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes zoomIn {
    from {
        transform: scale(0.5);
        opacity: 0;
    }
    to {
        transform: scale(1);
        opacity: 1;
    }
}

/* Standard Mobile View (max-width: 768px) */
@media (max-width: 768px) {
    .container {
        padding: 15px;
        height: auto;
    }

    .container-name h1,
    .container-name h2 {
        font-size: 2.5rem;
    }

    .container-name h3 {
        font-size: 1.5rem;
    }

    .container-name h4 {
        font-size: 1.2rem;
    }

    .container-name p {
        font-size: 0.9rem;
        line-height: 1.4;
    }

    .container-name img {
      display: none;
        
    }

    .profile img {
        width: 120px;
        height: 120px;
    }

    .container-name .social-icons {
        font-size: 1.5rem;
        gap: 15px;
    }

    /* Hides extra profile image section */
    .profile {
        display: none;
    }
}

/* Large Mobile View (max-width: 1024px) */
@media (max-width: 1024px) {
    .container {
        padding: 20px;
    }

    .container-name h1,
    .container-name h2 {
        font-size: 3.5rem;
    }

    .container-name h3 {
        font-size: 1.8rem;
    }

    .container-name h4 {
        font-size: 1.4rem;
    }

    .container-name p {
        font-size: 1rem;
        line-height: 1.5;
    }

    .container-name img {
        margin-left: 700px;
        margin-top: -400px;
        width: 250px;
        height: 250px;
    }

    .profile img {
        width: 130px;
        height: 130px;
    }

    .container-name .social-icons {
        font-size: 1.75rem;
        gap: 18px;
    }
}

/* Desktop and Larger Devices (Optional for further customization) */
@media (min-width: 1025px) {
    .container {
        flex-direction: row;
        justify-content: center;
        gap: 50px;
    }

    .container-name {
        text-align: left;
    }

    .container-name img {
        width: 300px;
        height: 300px;
    }

    .container-name h1 {
        font-size: 4rem;
    }

    .container-name h2 {
        font-size: 4.5rem;
    }

    .container-name h3 {
        font-size: 2rem;
    }

    .container-name h4 {
        font-size: 1.6rem;
    }

    .container-name p {
        font-size: 1.1rem;
        line-height: 1.8;
    }

    .container-name .social-icons {
        font-size: 2rem;
    }
}
