.service {
    background-color: #000000;
    padding: 50px 20px;
    height: 90vh;
    text-align: center;
    animation: fadeIn 1.5s ease-in-out;
    font-family: "Nunito", sans-serif;
}

.service-title h2 {
    font-size: 2.5rem;
    color: #ffffff;
    margin-bottom: 10px;
    animation: slideInDown 1s ease-in-out;
    margin-right: 850px;
}

.service-title p {
    font-size: 1.2rem;
    color: #777;
    margin-bottom: 30px;
    animation: slideInDown 1.2s ease-in-out;
    margin-right: 815px;
}

.services-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    padding: 50px;
    animation: slideInUp 1.5s ease-in-out;
}

.services-format {
    background-color: #000000;
    border: 1px solid #dddddd00;
    border-radius: 10px;
    padding: 20px;
    text-align: start;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.4s ease, box-shadow 0.4s ease;
    animation: slideInLeft 1s ease-in-out;
}

.services-format h2 {
    font-size: 1.8rem;
    color: #3a8a8a;
    margin-bottom: 15px;
}

.services-format p {
    font-size: 1rem;
    color: #b0aeae;
    line-height: 1.5;
}

.services-format:hover {
    transform: scale(1.05);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

/* Animations */
@keyframes slideInLeft {
    from {
        transform: translateX(-100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slideInRight {
    from {
        transform: translateX(100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes zoomIn {
    from {
        transform: scale(0.5);
        opacity: 0;
    }
    to {
        transform: scale(1);
        opacity: 1;
    }
}

/* Media Queries for Responsiveness */
@media screen and (max-width: 768px) {
    .service {
        padding: 30px 15px;
    }

    .service-title h2 {
        font-size: 2rem;
        margin-right: 0;
    }

    .service-title p {
        font-size: 1rem;
        margin-right: 0;
    }

    .services-container {
        padding: 30px;
    }

    .services-format {
        padding: 15px;
    }

    .services-format h2 {
        font-size: 1.5rem;
    }

    .services-format p {
        font-size: 0.9rem;
    }
}

@media screen and (max-width: 480px) {
    .service {
        padding: 20px 10px;
    }

    .service-title h2 {
        font-size: 1.6rem;
        margin-right: 0;
    }

    .service-title p {
        font-size: 0.9rem;
        margin-right: 0;
    }

    .services-container {
        padding: 20px;
    }

    .services-format {
        padding: 10px;
    }

    .services-format h2 {
        font-size: 1.2rem;
    }

    .services-format p {
        font-size: 0.8rem;
    }
}
